    justify-content: center;
    flex-direction: column;
    margin-top: 30px;
margin-bottom: 30px;    
/*    background-color: rgb(82, 78, 78);*/
    opacity: 0.7;
   /* box-shadow: 0 0 0 0.65px grey;*/
}


.both{ 
margin-top: 30px;
width:50%;
display: flex;
align-items: center;
justify-content:space-around;
}

.boxes{
    margin-top: -40px;
}

form{
