.socialmedia p {
    margin-top: 5px;
    font-size: 60px;
    font-weight: bold;
    color: white;


}

.socialmedia a {
    color: aquamarine;
}

.socialmedia a:hover {
    color: rgb(67, 190, 149);
}


.socialmedia span {
    color: #15f4ee;
    margin-top: 15px;
    letter-spacing: 2px;
}



.button {
    display: flex;
    margin-top: 12px;
}


.profilepic {
    position: relative;
    color: aliceblue;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 4px 0px 0px white;
    height: 310px;
    width: 280px;
    border-radius: 50%;
    margin: 0 0px 0 100px;
    margin-top: 30px;
   margin-left: 0px;
}


.profilepic-bcg {
    height: 92%;
    width: 92%;
    background-image: url("../Portfolio/pics/nw.jpg");
    background-size: cover;
    border-radius: 50%;
    background-repeat: no-repeat;
    background-position: center;

}

.profilepic-bcg:hover {
    transform: scale(1.07);
    transition: 1s ease-out;
}


